import { DeleteOutlined, EditOutlined, PlusOutlined } from "@ant-design/icons";
import Icon from "@ant-design/compatible/lib/icon";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import {
  CError,
  CInput,
  CLoadingSpinner,
  CModal,
  CSelectIcon,
  CTable,
  CTitle,
} from "@components";
import { FGetAccess, FGetTableData, FRemove, FSubmit } from "@function";
import { Button, Col, Row } from "antd";
import CApi from "@api";

const DataMenu = () => {
  const router = useRouter();
  const [data, setData] = useState([]);
  const [accessList, setAccessList] = useState({ viewAny: true });
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isModal, setIsModal] = useState(false);
  const [isSubmit, setIsSubmit] = useState(false);
  const [listIcons, setListIcons] = useState([]);
  const [errors, setErrors] = useState({});
  const [forms, setForms] = useState({
    id: "",
    label: "",
    icon: "",
    path: "",
  });
  const [paginate, setPaginate] = useState({
    pageSize: 15,
    current_page: 1,
    keyword: "",
    total: 0,
  });

  const handleInsert = (e) => {
    setIsModal(!isModal);
  };

  const handleEdit = (e) => {
    setIsModal(!isModal);
    setForms({
      id: e.id,
      label: e.label,
      icon: e.icon,
      path: e.path,
    });
  };

  function handleSearch(e) {
    setPaginate({
      ...paginate,
      [e.target.id]: e.target.value,
    });
  }

  const onShowSizeChange = (current, pageSize) => {
    setPaginate({
      ...paginate,
      pageSize: pageSize,
      current_page: current,
    });
  };

  const columns = [
    {
      title: "Label",
      key: "label",
      align: "center",
      ellipsis: true,
      render: (item) => (
        <Link
          style={{ color: "#1890ff" }}
          href={router.pathname + "/" + item.id}
        >
          {item.label}
        </Link>
      ),
    },
    {
      title: "Icon",
      key: "icon",
      align: "center",
      ellipsis: true,
      width: 100,
      render: (item) => <Icon type={item.icon} />,
    },
    {
      title: "Path",
      dataIndex: "path",
      key: "path",
      width: 150,
      align: "center",
      ellipsis: true,
    },
    {
      title: "Action",
      key: "id",
      width: 120,
      align: "center",
      fixed: "right",
      render: (item) => (
        <Row justify="center">
          <Col span={8}>
            <Button
              type="primary"
              style={{ backgroundColor: "#FAAD14" }}
              icon={<EditOutlined />}
              size="small"
              onClick={() => {
                handleEdit(item);
              }}
            />
          </Col>
          <Col span={8}>
            <Button
              onClick={() => {
                FRemove(item.id, `/api/menu`, getData);
              }}
              type="primary"
              icon={<DeleteOutlined />}
              size="small"
              danger
            />
          </Col>
        </Row>
      ),
    },
  ];

  const items = [
    {
      key: "1",
      label: (
        <div onClick={handleInsert}>
          <PlusOutlined /> Add Menu
        </div>
      ),
    },
  ];

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
    getIconList();
  }, []);
  useEffect(() => {
    getData();
  }, [paginate]);

  const getData = () => {
    FGetTableData(setLoading, "api/menu", paginate, setData, setTotal);
  };

  const onRefresh = () => {
    setPaginate({
      pageSize: 15,
      current_page: 1,
      keyword: "",
    });
    setIsModal(false);
    onCancel();
    getData();
  };
  const handleReset = () => {
    setIsModal(false);
    onCancel();
  };
  const getIconList = () => {
    CApi({
      method: "GET",
      url: "api/list-icon",
    })
      .then((res) => {
        setListIcons(res.data);
      })
      .catch((_err) => {});
  };

  const onCancel = () => {
    setForms({
      id: "",
      label: "",
      icon: "",
      path: "",
    });
  };

  const handleChange = (e) => {
    setForms({
      ...forms,
      [e.target.id]: e.target.value,
    });
  };

  const onIcons = (value) => {
    setForms({
      ...forms,
      icon: value,
    });
  };

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) {
    return (
      <CError
        status={403}
        subTitle="Sorry, you are not authorized to access this page."
        labelButton="Back"
        router={router}
      />
    );
  }

  return (
    <>
      <CTitle />
      <CTable
        total={total}
        onShowSizeChange={onShowSizeChange}
        data={data}
        columns={columns}
        items={items}
        scroll={{ y: 350 }}
        paginate={paginate}
        handleSearch={handleSearch}
        refresh={onRefresh}
        loading={loading}
        accessPermission={accessList}
      />
      <CModal
        id={forms.id}
        title="Menu"
        onCancel={handleReset}
        isSubmit={isSubmit}
        onSubmit={() =>
          FSubmit(
            "api/menu",
            forms,
            setErrors,
            getData,
            handleReset,
            setIsSubmit
          )
        }
        open={isModal}
      >
        <CInput
          value={forms.label}
          id="label"
          validate={errors.label}
          isRequired={true}
          label="Label"
          onChange={handleChange}
        />
        <CSelectIcon
          validate={errors.icon}
          value={forms.icon}
          id="icon"
          options={listIcons}
          isRequired={true}
          label="Icon"
          onChange={onIcons}
        />
        <CInput
          value={forms.path}
          validate={errors.path}
          id="path"
          isRequired={true}
          label="Path"
          onChange={handleChange}
        />
      </CModal>
    </>
  );
};

export default DataMenu;
